                    imgData: require("@/assets/order-4.png")
                }
                // {text:'返修/退货',imgData:require('@/assets/Orders_05.png')}
            ],
            data: "",
            user_header: true
        };
    },
    beforeRouteLeave(to, from, next) {
        this.$store.state.changeHomeScrollY = 0;
        this.$store.state.listData = [];
        this.$store.state.listInfo = {};
        this.$store.state.groupScrollY = 0;
        this.$store.state.groupData = [];
        document.body.scrollTop = 0;
        sessionStorage.removeItem("classId");
        next();
    },

    destroyed() {
        this.scrollWatch = false;
    },
    components: {
        Shopsn,
        footNav
    },
    methods: {
        toOrder(sta) {
            // if(sta == 5){
            //     this.$router.push('/repair')
            //     return;
            // }
            this.$store.state.status = sta;
            this.$router.push({
                name: "orderWrap",
                params: {
                    status: sta
                }
            });
        },
        toLink(link) {
            if (!sessionStorage.getItem("user_ID")) {
                this.$router.push({
                    path: "/LogoIn"
                });
                return;
            }

            function isString(str) {
                return typeof str == "string" && str.constructor == String;
            }
            if (isString(link)) {
                this.$router.push({
                    path: link
                });
            } else {
                switch (link) {
                    case 0:
                        this.$router.push("/Collection");
                        break;
                    case 1:
                        this.$router.push("/groupOrder");
                        break;
                    case 2:
                        this.$router.push("/coupon");
                        this.$store.state.couponStatus = "";
                        break;
                    case 3:
                        this.$router.push("/myWallet");
                        break;

                    case 4:
                        this.$router.push("/myComment");
                        break;
                    case 5:
                        this.$router.push({
                            name: "address",
                            params: {
                                status: 2
                            }
                        });
                        break;
                    case 6:
                        this.$router.push("/Feedback");
                        break;
                    case 7:
                        this.$router.push("/code");
                        break;
                    case 8:
                        this.$router.push("/team");
                        break;
                    case 9:
                        this.$router.push("/have");
                        break;
                    // case 8:
                    //     this.$router.push('/rebateAbnormal');
                    //     break;
                    case 10:
                        this.$router.push("/follow");
                        break;
                }
            }
        }
    },
    mounted() {
      
        if (!sessionStorage.getItem("user_ID")) {
            this.$router.push({
                path: "/LogoIn"
            });
            return;
            document.body.scrollTop = 0;
            console.log(this.$store.state);
        }
        this.axios
            .post(
                API_URL + "Home/Pcenter/userinfo",
                qs.stringify({
                    app_user_id: sessionStorage.getItem("user_ID")
                })
            )
            .then(res => {
                this.data = res.data.data;
                let ua = window.navigator.userAgent.toLowerCase();
                this.$store.state.user_Imag = res.data.data.user_header;
            })
            .catch(err => {
                console.log(err);
            });
        // this.axios.post(API_URL + 'Home/Pcenter/my_wallet',qs.stringify({
        //     app_user_id:sessionStorage.getItem('user_ID'),
        // })).then((res) => {
        //     this.$store.state.my_wallet = res.data.data;
        // }).catch((err) => {
        //     console.log(err)
        // });
    }
};
